/* reset */
/* main variables */
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap");
html { font-size: 16px !important; }

body { margin: 0px; padding: 0px; font-family: "Open Sans", sans-serif; background: #fff; height: 100%; min-height: 100%; color: #000000; line-height: 1.2; position: relative; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

/*MAIN*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; font-size: 100%; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main { display: block; }

blockquote, q { quotes: none; }

blockquote:before, blockquote:after, q:before, q:after { content: ''; content: none; }

table { border-collapse: collapse; border-width: 0px; padding: 0px; margin: 0px; }

html { height: 100%; -webkit-text-size-adjust: none; -ms-text-size-adjust: none; }

input, textarea { color: #333; font-family: "Open Sans", sans-serif; outline: none; border-radius: 0; -moz-border-radius: 0; -webkit-border-radius: 0; -webkit-appearance: none; }

input[type="button"], input[type="submit"], button { cursor: pointer; }

td { margin: 0px; padding: 0px; }

form { padding: 0px; margin: 0px; }

a { color: #000; -webkit-transition: all 0.3s linear; -moz-transition: all 0.3s linear; -ms-transition: all 0.3s linear; -o-transition: all 0.3s linear; transition: all 0.3s linear; text-decoration: none; outline: none; -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

a:hover { color: #ff2d2d; }

a, span, div, button { outline: none !important; }

img { vertical-align: middle; max-width: 100%; }

input[type=submit], input[type=button], button { -webkit-appearance: none; outline: none; }

* { -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; }

*:before, *:after { -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; }

.clearfix:after, .wrapper:after, .row:after { content: "."; display: block; height: 0; clear: both; visibility: hidden; }

/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; margin-left: auto; margin-right: auto; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; position: relative; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-arrow.slick-disabled { display: none; }

.slick-arrow { position: absolute; color: #f95c39; margin-top: -20px; border: none; padding: 0; top: 50%; width: 39px; z-index: 30; cursor: pointer; height: 39px; font-size: 15px; border-radius: 6px; background: #fff; box-shadow: 2px 2px 13px rgba(0, 47, 76, 0.13); }

.slick-arrow:hover { background: #f95c39; color: #fff; }

.slick-disabled { visibility: hidden; opacity: 0; }

.slick-prev { left: 10px; }

.slick-next { right: 10px; }

.slick-dots { text-align: center; bottom: 2.2rem; left: 0; right: 0; font-size: 0; position: absolute; }

.slick-dots li { list-style: none; display: inline-block; list-style: none; padding: 9px; margin: -9px 0; cursor: pointer; }

.slick-dots li button { border: none; display: block; padding: 0; font-size: 0; width: 35px; height: 2px; background: #B7B7B7; transition: all 0.3s linear; }

.slick-dots li button:hover { background: #2D2D2D; }

.slick-dots li:hover button { background: #2D2D2D; }

.slick-dots .slick-active button { background: #3BB9D1; }

/* checkbox */
/* radio */
/* select */
.jq-checkbox { width: 16px; display: inline-block; height: 16px; border-radius: 4px; background: #fff; vertical-align: middle; cursor: pointer; border: 2px solid #cccccc; margin-right: 11px; top: -2px; }

.jq-checkbox.checked { border-color: #3399cc; }

.jq-checkbox.checked .jq-checkbox__div { width: 8px; height: 8px; margin: 2px 0 0 2px; border-radius: 2px; background: #3399cc; }

.jq-checkbox.disabled { opacity: 0.5; }

.jq-radio { width: 16px; display: inline-block; height: 16px; border-radius: 50%; background: #fff; vertical-align: middle; cursor: pointer; border: 2px solid #cccccc; margin-right: 11px; top: -2px; }

.jq-radio.checked { border-color: #3399cc; }

.jq-radio.checked .jq-radio__div { width: 8px; height: 8px; margin: 2px 0 0 2px; border-radius: 50%; background: #3399cc; }

.jq-radio.disabled { opacity: 0.5; }

.jq-file { width: 100%; box-shadow: none; border-radius: 5px; border: 1px dashed #b3b3b3; padding: 28px 23px; }

.jq-file input { cursor: pointer; height: auto; line-height: 1em; }

.jq-file__name { -moz-box-sizing: border-box; box-sizing: border-box; padding-right: 6px; float: left; font-size: 16px; white-space: nowrap; text-overflow: ellipsis; overflow: hidden; box-shadow: none; background: transparent; outline: none; color: #4d4d4d; letter-spacing: 2.7px; }

.jq-file__browse { position: relative; padding: 0 10px; border-left: none; border-radius: 0; background: transparent; box-shadow: none; color: #333; text-shadow: none; font-size: 16px; color: #00aeef; letter-spacing: 2.7px; }

.jq-file:hover .jq-file__browse { color: #00cccc; }

.jq-file:active .jq-file__browse { color: #00cccc; }

.jq-file.disabled .jq-file__name { color: #00cccc; }

.jq-file.disabled, .jq-file.disabled .jq-file__browse { border-color: #CCC; background: #F5F5F5; box-shadow: none; color: #888; }

.jq-selectbox { vertical-align: middle; cursor: pointer; width: 100%; }

.jq-selectbox__select { height: 37px; padding: 0px 24px 0px 12px; border: 0px solid #9f9f9f; border-radius: 4px; background: #fff; line-height: 37px; color: #09183a; z-index: 1; font-family: "Open Sans", sans-serif; font-size: 14px; font-weight: bold; box-shadow: 2px 2px 7px rgba(0, 47, 76, 0.1); }

.jq-selectbox.disabled .jq-selectbox__select { border-color: #CCC; background: #F5F5F5; box-shadow: none; color: #888; }

.jq-selectbox__select-text { display: block; width: 100%; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }

.jq-selectbox__trigger { position: absolute; top: 0; right: 0; width: 34px; height: 100%; }

.jq-selectbox__trigger-arrow { position: absolute; top: 16px; right: 9px; border: 4px solid transparent; border-top: 5px solid #09183a; }

.jq-selectbox.opened .jq-selectbox__trigger-arrow { transform: rotate(180deg); top: 13px; }

.jq-selectbox__dropdown { top: 40px !important; width: 100%; box-sizing: border-box; line-height: 24px; border: 0px solid #dcdcdc; background: #FFF; border-radius: 4px; box-shadow: 2px 2px 13px rgba(17, 48, 74, 0.1); font-weight: bold; }

.jq-selectbox__search { margin: 5px; }

.jq-selectbox__search input { -moz-box-sizing: border-box; box-sizing: border-box; width: 100%; margin: 0; padding: 5px 27px 6px 8px; outline: none; border: 1px solid #CCC; border-radius: 3px; background: url(data:image/png;base64,) no-repeat 100% 50%; box-shadow: inset 1px 1px #F1F1F1; color: #333; -webkit-appearance: textfield; }

.jq-selectbox__search input::-webkit-search-cancel-button, .jq-selectbox__search input::-webkit-search-decoration { -webkit-appearance: none; }

.jq-selectbox__not-found { margin: 5px; padding: 5px 8px 6px; background: #F0F0F0; font-size: 13px; }

.jq-selectbox ul { margin: 0; padding: 0; }

.jq-selectbox li { min-height: 18px; padding: 0px 12px 0px 12px; line-height: 37px; }

.jq-selectbox li:hover { color: #f95c39; }

.jq-selectbox li.selected, .jq-selectbox li.sel { background-color: #f2f5f7; color: #191e38; }

.jq-selectbox li.disabled { color: #ccc; background: none; }

.jq-selectbox li.disabled:hover { background: none; }

.jq-selectbox li.optgroup { font-weight: bold; }

.jq-selectbox li.optgroup:hover { background: none; color: #231F20; cursor: default; }

.jq-selectbox li.option { padding-left: 25px; }

.jq-select-multiple { -moz-box-sizing: border-box; box-sizing: border-box; padding: 1px; border: 1px solid #CCC; border-bottom-color: #B3B3B3; border-radius: 4px; box-shadow: inset 1px 1px #F1F1F1, 0 1px 2px rgba(0, 0, 0, 0.1); background: #FFF; color: #333; cursor: default; }

.jq-select-multiple.focused { border: 1px solid #5794BF; }

.jq-select-multiple.disabled { border-color: #CCC; background: #F5F5F5; box-shadow: none; color: #888; }

.jq-select-multiple ul { margin: 0; padding: 0; }

.jq-select-multiple li { padding: 3px 9px 4px; list-style: none; }

.jq-select-multiple li:first-child { border-radius: 3px 3px 0 0; }

.jq-select-multiple li:last-child { border-radius: 0 0 3px 3px; }

.jq-select-multiple li.selected { background: #08C; color: #FFF; }

.jq-select-multiple li.disabled { color: #AAA; }

.jq-select-multiple.disabled li.selected, .jq-select-multiple li.selected.disabled { background: #CCC; color: #FFF; }

.jq-select-multiple li.optgroup { font-weight: bold; }

.jq-select-multiple li.option { padding-left: 25px; }

/*
================================================================================
|                                     HEADER                                   |
================================================================================
*/
.header { width: 100%; order: 1; -webkit-order: 1; position: fixed; left: 0; right: 0; z-index: 50; top: 30px; padding: 10px 0 10px; transition: all 0.3s linear; }

.header.fix { padding: 0px 0; top: 0; }

.header.fix .nav li a { padding: 8px 30px; }

.header:before { content: ''; opacity: 0; transition: all 0.3s linear; position: absolute; top: -30px; right: -100%; bottom: -60px; background: rgba(0, 0, 0, 0.9); }

.first_nav { display: flex; flex-direction: row; justify-content: center; transition: all 0.3s linear; }

.nav { background: #313030; width: 100%; transition: all 0.3s linear; display: flex; flex-direction: row; justify-content: center; align-items: center; }

.nav li { list-style: none; }

.nav li a { display: block; padding: 40px 30px; font-weight: bold; font-size: 14px; color: #e0e0e0; text-transform: uppercase; }

.nav li.active a, .nav li:hover a { background: #ff3f40; color: #fff; }

.mobile-nav { position: absolute; top: -10px; right: 20px; width: 40px; height: 40px; display: none; transition: all 0.3s linear; }

.mobile-nav span { height: 3px; display: block; width: 100%; background: #fff; transition: all 0s linear; position: absolute; top: 50%; transform: translateY(-50%); }

.mobile-nav span:before { height: 3px; display: block; content: ''; width: 100%; background: #fff; transition: all 0.5s linear; position: absolute; left: 0; top: -10px; }

.mobile-nav span:after { height: 3px; display: block; content: ''; width: 100%; background: #fff; transition: all 0.5s linear; position: absolute; left: 0; bottom: -10px; }

.header.active { position: absolute; top: 0; left: 0; right: 0; bottom: 0; transition: all 0.3s linear; }

.header.active .first_nav { position: absolute; top: 66px; left: 0; right: 0; display: block; }

.header.active .nav { flex-direction: column; background: none; }

.header.active .nav li { margin: 10px; width: 100%; margin: 0; }

.header.active .nav li a { text-align: center; }

.header.active .mobile-nav { top: 20px; }

.header.active .mobile-nav span { background: transparent; }

.header.active .mobile-nav span:before { transform: rotate(45deg); top: 0px; }

.header.active .mobile-nav span:after { transform: rotate(-45deg); bottom: 0px; }

.header.active:before { opacity: 1; width: 100%; border-radius: 0; right: 0; }

body.active { overflow: hidden; }

@media screen and (max-width: 559px) { .mobile-nav { display: block; }
  .first_nav { display: none; }
  .header.fix { top: 30px; padding: 10px 0 10px; position: absolute; } }

.footer { text-align: left; width: 100%; min-width: 320px; order: 3; -webkit-order: 3; background: #313030; color: #b8b8b8; padding: 47px 0 31px; }

.footer_content { display: flex; flex-direction: row; justify-content: space-between; align-items: center; }

@keyframes soclink { 0% { top: 0;
    opacity: 1; }
  20% { top: -2px;
    opacity: 0.8; }
  40% { top: -8px;
    opacity: 0.6; }
  50% { top: -10px;
    opacity: 0.5; }
  60% { top: -8px;
    opacity: 0.6; }
  80% { top: -2px;
    opacity: 0.8; }
  100% { top: 0px; } }

.footer_soc { margin: 0 -9px; display: flex; flex-direction: row; align-items: center; justify-content: center; position: relative; }

.footer_soc li { list-style: none; margin: 0 9px; }

.footer_soc li a { display: block; width: 33px; height: 33px; }

.footer_soc li:hover { animation-name: soclink; animation-duration: 700ms; animation-iteration-count: infinite; animation-direction: alternate; position: relative; }

@media screen and (max-width: 599px) { .footer_content { flex-direction: column; }
  .copyright { order: 2; text-align: center; }
  .footer_soc { margin-bottom: 10px; } }

.main-wrapper { padding: 0 0 0 0; min-width: 320px; width: 100%; position: relative; overflow: hidden; min-height: 100%; display: flex; flex-direction: column; justify-content: flex-start; -webkit-justify-content: flex-start; -ms-flex-pack: start; transition: opacity 0.3s linear; }

.wrapper { min-width: 320px; max-width: 1249px; padding: 0 19px; margin: 0 auto; position: relative; /*z-index: 2; */ width: 100%; }

/* titles */
p { padding: 10px 0; }

h1, .h1 { font-size: 60px; line-height: 1.2; color: inherit; padding: 10px 0 10px 0; font-weight: 900; font-family: "Open Sans", sans-serif; text-transform: uppercase; }

h2, .h2 { font-size: 42px; line-height: 1.2; color: #02101a; padding: 10px 0; font-weight: bold; text-transform: uppercase; }

h3, .h3 { font-size: 30px; line-height: 1.2; color: inherit; padding: 10px 0; font-weight: bold; }

h4, .h4 { font-size: 24px; line-height: 1.2; color: inherit; padding: 10px 0 10px 0; position: relative; font-weight: bold; }

/*section*/
section { overflow: hidden; }

/* text position */
.text-left { text-align: left !important; }

.text-center { text-align: center !important; }

.text-right { text-align: right !important; }

.nowrap { white-space: nowrap !important; }

.upper { text-transform: uppercase; }

/* loader */
.loaded .main-wrapper { visibility: hidden; }

.icon-load { position: fixed; width: 100%; height: 100%; top: 0; left: 0; display: none; background-color: #fff; z-index: 200; }

.icon-load:after { content: ''; width: 850px; height: 200px; position: absolute; left: 50%; top: 50%; margin-left: -425px; margin-top: -100px; }

.loaded .icon-load { display: block; }

.loaded-text { position: absolute; left: 0; right: 0; top: 50%; margin-top: 5rem; text-align: center; z-index: 10; }

.wow { visibility: hidden; }

