.content { min-width: 320px; text-align: left; width: 100%; order: 2; -webkit-order: 2; flex-grow: 1; -webkit-flex-grow: 1; position: relative; z-index: 0; }

body.hid { overflow: hidden; }

.t-show { display: none; }

