.btn { display: inline-block; height: 55px; padding: 0 64px; line-height: 55px; text-align: center; background: transparent; font-weight: bold; color: #ff2d2d; position: relative; border-radius: 4px; overflow: hidden; border: none; transition: all 0.3s linear; }

