.organaisation_video_item iframe { width: 100%; height: 100%; position: absolute; top: 0; left: 0; z-index: 0; }

.organaisation_video_item_img { position: absolute; top: 0; left: 0; right: 0; bottom: 0; z-index: 1; display: flex; flex-direction: row; align-items: center; justify-content: center; }

.organaisation_video_item_img_play { display: block; width: 70px; height: 70px; background: url(../img/video_icon.png) no-repeat center; }

.section_provide { padding: 108px 0; overflow: hidden; }

.provide { position: relative; display: flex; flex-direction: row; position: relative; }

.provide:after { height: 2px; width: 100%; background: #ececec; display: block; content: ''; position: absolute; bottom: 0; }

.provide_des { width: 50%; padding-right: 100px; position: relative; }

.provide_des h2 { margin-bottom: 28px; }

.provide_des_txt { margin-bottom: 38px; }

.provide_manadg { width: 50%; position: relative; }

.manadg { display: flex; flex-direction: row; justify-content: flex-start; align-items: flex-start; flex-wrap: wrap; margin: 0 -25px; }

.manadg_item { width: calc(50% - 25px); margin-bottom: 25px; padding: 0 25px; }

.manadg_item h3 { font-weight: 600; font-size: 24px; color: #000000; opacity: 0.7; margin-bottom: 20px; }

.manadg_item_ico { margin-bottom: 32px; width: 44px; height: 44px; }

.section_products { padding: 110px 0; overflow: hidden; }

.section_products h2 { text-align: center; }

.top-text { max-width: 506px; position: relative; padding-bottom: 20px; text-align: center; margin: 0 auto 88px; }

.top-text:after { position: absolute; bottom: 0; left: 50%; content: ''; transform: translateX(-50%); height: 2px; width: 130px; background: #ff2d2d; }

.products_block { position: relative; }

.tabs { margin-bottom: 54px; display: flex; flex-direction: row; align-items: center; justify-content: center; position: relative; }

.tabs li { padding: 0 16px; list-style: none; }

.tabs li a { display: block; font-weight: 600; font-size: 22px; text-transform: uppercase; }

.tabs li.current a { color: #ff2d2d; }

.pane.hid { height: 0; padding: 0; margin: 0; overflow: hidden; }

.product { position: relative; display: flex; flex-direction: row; flex-wrap: wrap; margin: -10px; position: relative; }

.product_item { margin: 10px; width: calc(25% - 20px); position: relative; background: #f5f0dd; overflow: hidden; }

.product_item:after { content: ''; padding-top: 100%; display: block; }

.product_item img { position: absolute; top: 0; left: 0; width: 100%; max-width: 100%; max-height: 100%; transition: all 0.5s linear; }

.product_item:hover img { transform: scale(1.1); }

.section_meet { position: relative; padding-bottom: 108px; }

.meet .top-text { max-width: 464px; text-align: left; margin: 0 0 70px 0; }

.meet .top-text:after { transform: translateX(0); left: 0; }

.meet_block { display: flex; flex-direction: row; }

.meet_block_bigimg { min-width: 318px; width: 318px; overflow: hidden; background-color: #f5f0dd; margin-right: 74px; position: relative; display: flex; justify-content: center; align-items: center; }

.meet_block_bigimg img { max-width: 100%; max-height: 100%; }

.meet_block_wrap h2 { font-size: 30px; margin-bottom: 40px; }

.meet_block_txt { margin-bottom: 30px; }

.meet_block_ul { display: flex; margin-bottom: 60px; }

.meet_block_ul li { list-style: none; margin: 0 14px; }

.meet_block_ul li:first-child { margin-left: 0; }

.meet_block_ul li a { font-style: italic; }

.meet_block_persons { display: flex; flex-direction: row; }

.meet_block_persons_item { width: calc(25% - 14px); overflow: hidden; background: #f5f0dd; position: relative; }

.meet_block_persons_item:before { content: ''; display: block; padding-bottom: 100%; }

.meet_block_persons_item img { width: 100%; max-width: 100%; max-height: 100%; position: absolute; top: 0; left: 0; }

.meet_block_persons_item:first-child { margin-left: 0; }

.meet_block_persons_item:hover .meet_block_persons_item_name { opacity: 1; }

.meet_block_persons_item_name { position: absolute; bottom: 0; left: 0; right: 0; opacity: 0; transition: all 0.5s linear; background: rgba(0, 0, 0, 0.5); padding: 10px 5px; color: #fff; text-align: center; font-weight: 600; text-transform: uppercase; }

.cover_link { position: absolute; top: 0; left: 0; right: 0; bottom: 0; z-index: 1; display: block; }

.section_deliver { background-color: #f5f0dd; position: relative; }

.section_deliver:after { content: ''; }

.deliver { min-height: 512px; display: flex; flex-direction: column; justify-content: center; align-items: center; }

.deliver h2 { color: #fff; text-align: center; margin-bottom: 40px; }

.deliver_txt { text-align: center; color: #fff; font-size: 16px; position: relative; margin-bottom: 60px; }

.search_block { position: relative; width: 758px; margin: 0 auto; padding-right: 236px; }

.inp_search { width: 100%; height: 78px; background-color: #ffffff; border: none; border-radius: 4px; color: #000; padding: 4px 36px; line-height: 70px; }

.inp_search::-webkit-input-placeholder { color: #828282; }

.inp_search::-moz-placeholder { color: #828282; }

.inp_search:-ms-input-placeholder { color: #828282; }

.inp_search:-moz-placeholder { color: #828282; }

.btn-search { width: 215px; position: absolute; top: 0; right: 0; text-transform: uppercase; height: 78px; line-height: 78px; }

.section_feetback { padding-top: 108px; padding-bottom: 108px; }

.section_feetback h2 { margin-bottom: 50px; }

.section_feetback .top-text { text-align: left; margin: 0 0 76px 0; padding-bottom: 56px; max-width: 736px; }

.section_feetback .top-text:after { left: 0; transform: translateX(0); }

.feetback { display: flex; flex-direction: row; justify-content: space-between; }

.feetback_form { width: 50%; }

.feetback_form .form_row:last-child { margin: 0; }

.feetback_form textarea.form-control { height: 262px; }

.feetback_form .btn-red { width: 100%; text-transform: uppercase; }

.feetback_contact { height: 480px; position: relative; padding: 20px 80px; background: #2d2d2d; color: #fff; display: flex; flex-direction: column; align-items: flex-start; justify-content: center; }

.feetback_contact:after { content: ''; width: 100%; position: absolute; top: 0; right: 0; background: #2d2d2d; bottom: 0; margin-right: -100%; }

.feetback_contact_title { color: #ffffff; margin-bottom: 42px; text-transform: uppercase; }

.feetback_contact .contact_tel { text-decoration: none; color: #fff; }

/* */
.sectionbg { position: absolute; top: 0; left: 0; right: 0; bottom: 0; -webkit-filter: blur(5px); filter: blur(5px); overflow: hidden; }

.sectionbg:after { display: block; content: ''; position: absolute; top: 0; right: 0; left: 0; bottom: 0; background: rgba(0, 0, 0, 0.6); }

/*---------------kayframe-----------------*/
/*---------------------------------------*/
@media screen and (max-width: 1023px) { h1, .h1 { font-size: 48px; }
  h2, .h2 { font-size: 36px; }
  .section_organaisation { padding: 220px 0 50px; }
  .organaisation { flex-direction: column; }
  .organaisation_des { width: 100%; margin-bottom: 50px; }
  .organaisation_video { width: 100%; }
  .organaisation_video_item { width: 100%; }
  .organaisation_video_item:before { padding-top: 60%; }
  .section_provide { padding-bottom: 0; }
  .provide { flex-direction: column; }
  .provide_des { padding: 0; width: 100%; margin-bottom: 50px; }
  .provide_manadg { width: 100%; }
  .provide:after { display: none; }
  .tabs { margin-bottom: 40px; }
  .tabs li a { font-size: 18px; }
  .meet_block_bigimg { min-width: 234px; width: 234px; margin-right: 44px; }
  .feetback { flex-direction: column; }
  .feetback_form { width: 100%; margin-bottom: 40px; }
  .feetback_contact { width: 100%; }
  .feetback_contact:after { display: none; }
  .search_block { width: 700px; } }

@media screen and (max-width: 767px) { .search_block { width: -webkit-fill-available; width: 100%; }
  .meet_block_bigimg { min-width: 100%; width: 100%; margin-right: 0; margin-bottom: 40px; }
  .meet_block { flex-direction: column; }
  .tabs li a { font-size: 16px; }
  .tabs li { padding: 0 10px; }
  h1, .h1 { font-size: 36px; }
  h2, .h2 { font-size: 24px; }
  .meet_block_wrap h2 { font-size: 24px; margin-bottom: 18px; }
  .section_feetback { padding: 70px 0; }
  .inp_search { height: 50px; }
  .btn-search { height: 50px; line-height: 50px; }
  .section_provide { padding: 70px 0; }
  .section_products { padding: 70px 0; } }

@media screen and (max-width: 599px) { .manadg { flex-direction: column; }
  .manadg_item { width: calc(100% - 0px); }
  .tabs li a { font-size: 12px; }
  .tabs li { padding: 0 4px; }
  .product_item { width: calc(50% - 20px); }
  .meet_block_persons_item { width: calc(50% - 14px); margin-bottom: 14px; }
  .meet_block_persons { flex-wrap: wrap; justify-content: space-around; }
  .meet_block_persons_item:first-child { margin-left: initial; }
  .search_block { padding: 0; }
  .inp_search { margin-bottom: 10px; }
  .btn-search { width: 100%; position: relative; right: auto; top: auto; }
  .form_row_cols { flex-direction: column; }
  .form_col { width: 100%; }
  .form_col + .form_col { margin-top: 20px; }
  .form_row { margin-bottom: 20px; }
  .feetback_contact { height: auto; padding: 20px; }
  h1, .h1 { font-size: 26px; }
  h2, .h2 { font-size: 18px; }
  .manadg_item h3 { font-size: 18px; }
  .tabs { flex-direction: column; }
  .tabs li { margin-bottom: 10px; }
  .meet_block_ul { flex-direction: column; }
  .meet_block_ul li { margin: 0; margin-bottom: 10px; } }

